import React, { useState, useEffect, useCallback, useRef } from 'react';
import { Download, Plus, X, Check, Menu, LogOut, Undo, Redo, Share2 } from 'lucide-react';
import * as XLSX from 'xlsx';

// Mock backend - we'll replace this with Firebase later
const MOCK_PASSWORD = 'banyan';
const STORAGE_KEY = 'quizzes_data';

const mockBackend = {
  authenticate: (password) => {
    return password.toLowerCase() === MOCK_PASSWORD;
  },
  
  getQuizzes: () => {
    const data = localStorage.getItem(STORAGE_KEY);
    return data ? JSON.parse(data) : [];
  },
  
  saveQuiz: (quiz) => {
    const quizzes = mockBackend.getQuizzes();
    const index = quizzes.findIndex(q => q.id === quiz.id);
    
    if (index >= 0) {
      quizzes[index] = quiz;
    } else {
      quizzes.push(quiz);
    }
    
    localStorage.setItem(STORAGE_KEY, JSON.stringify(quizzes));
    return quiz;
  },
  
  deleteQuiz: (id) => {
    const quizzes = mockBackend.getQuizzes().filter(q => q.id !== id);
    localStorage.setItem(STORAGE_KEY, JSON.stringify(quizzes));
  }
};

const QuizCreator = () => {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [password, setPassword] = useState('');
  const [selectedUser, setSelectedUser] = useState('');
  const [rememberMe, setRememberMe] = useState(false);
  const [currentUser, setCurrentUser] = useState('');
  const [quizzes, setQuizzes] = useState([]);
  const [currentQuiz, setCurrentQuiz] = useState(null);
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const [saveTimeout, setSaveTimeout] = useState(null);
  const [history, setHistory] = useState([]);
  const [historyIndex, setHistoryIndex] = useState(-1);
  const [showDeleteConfirm, setShowDeleteConfirm] = useState(null);
  const [showCategoryModal, setShowCategoryModal] = useState(false);
  const [showPreview, setShowPreview] = useState(false);
  const [previewAnswers, setPreviewAnswers] = useState({});
  const [quizResults, setQuizResults] = useState(null);
  const [autoSaveEnabled, setAutoSaveEnabled] = useState(false);
  const [isSaved, setIsSaved] = useState(true);
  const [showSavedCheck, setShowSavedCheck] = useState(false);
  const [showDownloadWarning, setShowDownloadWarning] = useState(false);
  const [downloadWarnings, setDownloadWarnings] = useState([]);
  const [showShareModal, setShowShareModal] = useState(false);
  const [shareUrl, setShareUrl] = useState('');
  const [sharedQuizId, setSharedQuizId] = useState(null);
  const [viewMode, setViewMode] = useState('edit');
  const [expandedQuestions, setExpandedQuestions] = useState({});
  const [sidebarView, setSidebarView] = useState('current'); // 'current' or 'all'
  const [editingTitle, setEditingTitle] = useState(false);
  const [historyExpanded, setHistoryExpanded] = useState(false);
  const [editingUserName, setEditingUserName] = useState(false);
  const [newCategoryName, setNewCategoryName] = useState('');
  const [isSharedQuizPage, setIsSharedQuizPage] = useState(false);
  const [selectedCategoryFilter, setSelectedCategoryFilter] = useState('All');
  const inputRefs = useRef({});

  const userNames = ['Ben', 'Blake', 'Dustin', 'Jon', 'Luke', 'Melissa', 'Skyler'];

  // Check if user is already authenticated on mount
  useEffect(() => {
    const authToken = localStorage.getItem('quiz_auth');
    const savedUser = localStorage.getItem('quiz_user');
    
    // Check if there's a shared quiz ID in the URL
    const urlParams = new URLSearchParams(window.location.search);
    const shareId = urlParams.get('quiz');
    
    if (shareId) {
      setSharedQuizId(shareId);
      const allQuizzes = mockBackend.getQuizzes();
      const sharedQuiz = allQuizzes.find(q => q.shareId === shareId);
      if (sharedQuiz) {
        setCurrentQuiz(sharedQuiz);
        setHistory([JSON.parse(JSON.stringify(sharedQuiz))]);
        setHistoryIndex(0);
        setIsSaved(true);
        // If user is not authenticated, show as full page instead of preview modal
        if (authToken !== 'authenticated') {
          setIsSharedQuizPage(true);
          setViewMode('preview');
        } else {
          setViewMode('preview');
          setShowPreview(true);
        }
      }
    }
    
    if (authToken === 'authenticated' && savedUser) {
      setIsAuthenticated(true);
      setCurrentUser(savedUser);
    }
  }, []);

  useEffect(() => {
    if (isAuthenticated) {
      setQuizzes(mockBackend.getQuizzes());
    }
  }, [isAuthenticated]);

  const saveToHistory = useCallback((quiz) => {
    if (!quiz) return;
    
    setHistory(prev => {
      const newHistory = prev.slice(0, historyIndex + 1);
      newHistory.push(JSON.parse(JSON.stringify(quiz)));
      return newHistory.slice(-50);
    });
    setHistoryIndex(prev => Math.min(prev + 1, 49));
  }, [historyIndex]);

  const undo = useCallback(() => {
    if (historyIndex > 0) {
      const prevState = history[historyIndex - 1];
      setCurrentQuiz(JSON.parse(JSON.stringify(prevState)));
      setHistoryIndex(prev => prev - 1);
    }
  }, [history, historyIndex]);

  const redo = useCallback(() => {
    if (historyIndex < history.length - 1) {
      const nextState = history[historyIndex + 1];
      setCurrentQuiz(JSON.parse(JSON.stringify(nextState)));
      setHistoryIndex(prev => prev + 1);
    }
  }, [history, historyIndex]);

  useEffect(() => {
    const handleKeyDown = (e) => {
      if ((e.ctrlKey || e.metaKey) && e.key === 'z' && !e.shiftKey) {
        e.preventDefault();
        undo();
      }
      if (((e.ctrlKey || e.metaKey) && e.shiftKey && e.key === 'z') || 
          ((e.ctrlKey || e.metaKey) && e.key === 'y')) {
        e.preventDefault();
        redo();
      }
      if ((e.ctrlKey || e.metaKey) && e.key === 'Enter') {
        e.preventDefault();
        if (currentQuiz) addQuestion();
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, [undo, redo, currentQuiz]);

  const autoSave = useCallback((quiz) => {
    if (!autoSaveEnabled) return;
    
    if (saveTimeout) clearTimeout(saveTimeout);
    
    const timeout = setTimeout(() => {
      const savedQuiz = mockBackend.saveQuiz({
        ...quiz,
        lastModified: new Date().toISOString()
      });
      setQuizzes(prev => {
        const filtered = prev.filter(q => q.id !== savedQuiz.id);
        return [savedQuiz, ...filtered].sort((a, b) => 
          new Date(b.lastModified) - new Date(a.lastModified)
        );
      });
      setIsSaved(true);
    }, 1000);
    
    setSaveTimeout(timeout);
  }, [saveTimeout, autoSaveEnabled]);

  const manualSave = () => {
    if (!currentQuiz) return;
    
    const now = new Date().toISOString();
    const historyEntry = {
      user: currentUser,
      action: 'modified',
      timestamp: now
    };
    
    const savedQuiz = mockBackend.saveQuiz({
      ...currentQuiz,
      history: [...(currentQuiz.history || []), historyEntry],
      lastModified: now
    });
    
    setQuizzes(prev => {
      const filtered = prev.filter(q => q.id !== savedQuiz.id);
      return [savedQuiz, ...filtered].sort((a, b) => 
        new Date(b.lastModified) - new Date(a.lastModified)
      );
    });
    
    setCurrentQuiz(savedQuiz);
    setIsSaved(true);
    setShowSavedCheck(true);
    
    setTimeout(() => {
      setShowSavedCheck(false);
    }, 2000);
  };

  const handleLogin = () => {
    if (!selectedUser) {
      alert('Please select your name');
      return;
    }
    if (mockBackend.authenticate(password)) {
      setIsAuthenticated(true);
      setCurrentUser(selectedUser);
      if (rememberMe) {
        localStorage.setItem('quiz_auth', 'authenticated');
        localStorage.setItem('quiz_user', selectedUser);
      }
      
      if (sharedQuizId && viewMode === 'preview') {
        setShowPreview(false);
        setViewMode('edit');
      }
    } else {
      alert('Incorrect password');
    }
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      handleLogin();
    }
  };

  const toggleQuestionExpanded = (questionId) => {
    setExpandedQuestions(prev => ({
      ...prev,
      [questionId]: !prev[questionId]
    }));
  };

  const createNewQuiz = () => {
    const now = new Date().toISOString();
    const newQuiz = {
      id: Date.now().toString(),
      title: 'Untitled Quiz',
      category: '',
      status: 'Draft',
      tags: [],
      questions: [{
        id: Date.now().toString(),
        question: '',
        answers: [{ text: '', correct: false }]
      }],
      history: [{
        user: currentUser,
        action: 'created',
        timestamp: now
      }],
      lastModified: now
    };
    setCurrentQuiz(newQuiz);
    setHistory([JSON.parse(JSON.stringify(newQuiz))]);
    setHistoryIndex(0);
    setIsSaved(false);
    setSidebarOpen(false);
  };

  const updateQuiz = (updates) => {
    const updated = { ...currentQuiz, ...updates };
    
    if (updates.status && updates.status !== currentQuiz.status) {
      const historyEntry = {
        user: currentUser,
        action: `changed status to "${updates.status}"`,
        timestamp: new Date().toISOString()
      };
      updated.history = [...(currentQuiz.history || []), historyEntry];
    }
    
    setCurrentQuiz(updated);
    saveToHistory(updated);
    setIsSaved(false);
    autoSave(updated);
  };

  const addQuestion = () => {
    const newQuestion = {
      id: Date.now().toString(),
      question: '',
      answers: [{ text: '', correct: false }]
    };
    updateQuiz({
      questions: [...currentQuiz.questions, newQuestion]
    });
    // Auto-expand the new question
    setExpandedQuestions(prev => ({
      ...prev,
      [newQuestion.id]: true
    }));
  };

  const updateQuestion = (qIndex, updates) => {
    const questions = [...currentQuiz.questions];
    questions[qIndex] = { ...questions[qIndex], ...updates };
    updateQuiz({ questions });
  };

  const deleteQuestion = (qIndex) => {
    if (currentQuiz.questions.length === 1) {
      alert('Quiz must have at least one question');
      return;
    }
    updateQuiz({
      questions: currentQuiz.questions.filter((_, i) => i !== qIndex)
    });
  };

  const moveQuestion = (qIndex, direction) => {
    const newIndex = direction === 'up' ? qIndex - 1 : qIndex + 1;
    if (newIndex < 0 || newIndex >= currentQuiz.questions.length) return;
    
    const questions = [...currentQuiz.questions];
    [questions[qIndex], questions[newIndex]] = [questions[newIndex], questions[qIndex]];
    updateQuiz({ questions });
  };

  const addAnswer = (qIndex) => {
    const question = currentQuiz.questions[qIndex];
    if (question.answers.length >= 4) return;
    
    updateQuestion(qIndex, {
      answers: [...question.answers, { text: '', correct: false }]
    });
  };

  const updateAnswer = (qIndex, aIndex, updates) => {
    const question = currentQuiz.questions[qIndex];
    const answers = [...question.answers];
    answers[aIndex] = { ...answers[aIndex], ...updates };
    updateQuestion(qIndex, { answers });
  };

  const deleteAnswer = (qIndex, aIndex) => {
    const question = currentQuiz.questions[qIndex];
    if (question.answers.length === 1) return;
    
    updateQuestion(qIndex, {
      answers: question.answers.filter((_, i) => i !== aIndex)
    });
  };

  const confirmDeleteQuiz = (quiz) => {
    setShowDeleteConfirm(quiz);
  };

  const deleteQuizConfirmed = () => {
    if (showDeleteConfirm) {
      mockBackend.deleteQuiz(showDeleteConfirm.id);
      setQuizzes(prev => prev.filter(q => q.id !== showDeleteConfirm.id));
      if (currentQuiz?.id === showDeleteConfirm.id) {
        setCurrentQuiz(null);
      }
      setShowDeleteConfirm(null);
    }
  };

  const addTag = (tag) => {
    if (!tag.trim()) return;
    const tags = currentQuiz.tags || [];
    if (!tags.includes(tag.trim())) {
      updateQuiz({ tags: [...tags, tag.trim()] });
    }
  };

  const removeTag = (tag) => {
    const tags = currentQuiz.tags || [];
    updateQuiz({ tags: tags.filter(t => t !== tag) });
  };

  const getAllCategories = () => {
    const categories = new Set();
    quizzes.forEach(quiz => {
      if (quiz.category) categories.add(quiz.category);
    });
    return Array.from(categories).sort();
  };

  const getFilteredQuizzes = () => {
    if (selectedCategoryFilter === 'All') {
      return quizzes.filter(quiz => quiz.status !== 'Archive');
    } else if (selectedCategoryFilter === 'Archive') {
      return quizzes.filter(quiz => quiz.status === 'Archive');
    } else {
      return quizzes.filter(quiz => quiz.category === selectedCategoryFilter && quiz.status !== 'Archive');
    }
  };

  const getCategoryFilterOptions = () => {
    const categories = getAllCategories();
    return ['All', ...categories, 'Archive'];
  };

  const togglePreviewAnswer = (questionId, answerIndex) => {
    setPreviewAnswers(prev => {
      const key = `${questionId}-${answerIndex}`;
      const newAnswers = { ...prev };
      if (newAnswers[key]) {
        delete newAnswers[key];
      } else {
        newAnswers[key] = true;
      }
      return newAnswers;
    });
  };

  const calculateScore = () => {
    let correct = 0;
    let total = currentQuiz.questions.length;
    
    currentQuiz.questions.forEach(question => {
      const correctAnswers = question.answers
        .map((a, i) => ({ ...a, index: i }))
        .filter(a => a.correct);
      
      const selectedAnswers = question.answers
        .map((a, i) => ({ ...a, index: i }))
        .filter((a, i) => previewAnswers[`${question.id}-${i}`]);
      
      const allCorrectSelected = correctAnswers.every(ca => 
        selectedAnswers.some(sa => sa.index === ca.index)
      );
      const noIncorrectSelected = selectedAnswers.every(sa => 
        correctAnswers.some(ca => ca.index === sa.index)
      );
      
      if (allCorrectSelected && noIncorrectSelected && correctAnswers.length > 0) {
        correct++;
      }
    });
    
    return { correct, total, percentage: Math.round((correct / total) * 100) };
  };

  const generateShareUrl = () => {
    if (!currentQuiz) return;
    
    let updatedQuiz = { ...currentQuiz };
    if (!updatedQuiz.shareId) {
      updatedQuiz.shareId = Math.random().toString(36).substring(2, 15) + Math.random().toString(36).substring(2, 15);
      mockBackend.saveQuiz(updatedQuiz);
      setCurrentQuiz(updatedQuiz);
    }
    
    const baseUrl = window.location.origin + window.location.pathname;
    const url = `${baseUrl}?quiz=${updatedQuiz.shareId}`;
    setShareUrl(url);
    setShowShareModal(true);
  };

  const copyShareUrl = () => {
    navigator.clipboard.writeText(shareUrl);
    alert('Link copied to clipboard!');
  };

  const handleEditFromPreview = () => {
    if (!isAuthenticated) {
      setShowPreview(false);
    } else {
      setShowPreview(false);
      setViewMode('edit');
    }
  };

  const exportToExcel = () => {
    if (!currentQuiz) return;

    const warnings = [];
    
    if (currentQuiz.title === 'Untitled Quiz') {
      warnings.push('Quiz title is still "Untitled Quiz". Please give your quiz a proper title.');
    }
    
    currentQuiz.questions.forEach((q, index) => {
      const hasCorrectAnswer = q.answers.some(a => a.correct);
      if (!hasCorrectAnswer) {
        warnings.push(`Question ${index + 1} does not have any correct answer marked.`);
      }
    });
    
    if (warnings.length > 0) {
      setDownloadWarnings(warnings);
      setShowDownloadWarning(true);
      return;
    }

    const wb = XLSX.utils.book_new();
    const data = [];
    
    data.push(['Quiz Title:', currentQuiz.title]);
    if (currentQuiz.category) data.push(['Category:', currentQuiz.category]);
    if (currentQuiz.status) data.push(['Status:', currentQuiz.status]);
    data.push([]);
    
    currentQuiz.questions.forEach((q, i) => {
      data.push([`Question ${i + 1}:`, q.question]);
      q.answers.forEach((a, j) => {
        data.push([
          `Answer ${j + 1}:`,
          a.text,
          a.correct ? 'CORRECT' : ''
        ]);
      });
      data.push([]);
    });

    const ws = XLSX.utils.aoa_to_sheet(data);
    const sheetName = currentQuiz.title.slice(0, 31).replace(/[:\\/?*\[\]]/g, '');
    XLSX.utils.book_append_sheet(wb, ws, sheetName);

    XLSX.writeFile(wb, `${currentQuiz.title}.xlsx`);
  };

  // If this is a shared quiz page for non-authenticated users, show full page
  if (isSharedQuizPage && !isAuthenticated && currentQuiz) {
    return (
      <div className="min-h-screen bg-gray-50 flex flex-col">
        {/* Header Ribbon */}
        <div className="bg-blue-600 text-white p-4 shadow-sm">
          <div className="max-w-4xl mx-auto flex items-center justify-between">
            <h1 className="text-xl font-bold">Quiz Pro</h1>
            <button
              onClick={() => {
                setIsSharedQuizPage(false);
                setCurrentQuiz(null);
                setSharedQuizId(null);
                // Clear URL parameters
                window.history.replaceState({}, document.title, window.location.pathname);
              }}
              className="bg-white text-blue-600 px-4 py-2 rounded-lg hover:bg-gray-100 font-medium"
            >
              Edit Quiz
            </button>
          </div>
        </div>

        {/* Quiz Content */}
        <div className="flex-1 p-6">
          <div className="max-w-4xl mx-auto">
            <div className="bg-white rounded-lg shadow-sm p-6">
              <div className="mb-6">
                <h2 className="text-2xl font-bold text-gray-900 mb-2">{currentQuiz.title}</h2>
                {currentQuiz.category && (
                  <span className="inline-block px-3 py-1 bg-blue-100 text-blue-700 rounded-full text-sm">
                    {currentQuiz.category}
                  </span>
                )}
              </div>

              <div className="space-y-6">
                {currentQuiz.questions.map((question, qIndex) => {
                  const questionResult = quizResults?.details.find(d => d.questionIndex === qIndex);
                  const showResults = !!quizResults;
                  
                  return (
                    <div key={question.id} className="border-b pb-6 last:border-b-0">
                      <div className="flex items-start gap-3 mb-3">
                        <h3 className="font-medium text-lg flex-1">
                          {qIndex + 1}. {question.question || '(No question text)'}
                        </h3>
                        {showResults && (
                          <div className={`px-3 py-1 rounded-full text-sm font-medium ${
                            questionResult?.isCorrect 
                              ? 'bg-green-100 text-green-700' 
                              : 'bg-red-100 text-red-700'
                          }`}>
                            {questionResult?.isCorrect ? ' Correct' : ' Incorrect'}
                          </div>
                        )}
                      </div>
                      <div className="space-y-2">
                        {question.answers.map((answer, aIndex) => {
                          const isUserSelected = !!previewAnswers[`${question.id}-${aIndex}`];
                          const isCorrectAnswer = answer.correct;
                          
                          let borderColor = 'border-gray-200';
                          let bgColor = 'bg-white';
                          let textColor = 'text-gray-900';
                          
                          if (showResults) {
                            if (isCorrectAnswer && isUserSelected) {
                              // User selected correct answer
                              borderColor = 'border-green-500';
                              bgColor = 'bg-green-50';
                              textColor = 'text-green-900';
                            } else if (isCorrectAnswer && !isUserSelected) {
                              // Correct answer not selected
                              borderColor = 'border-green-300';
                              bgColor = 'bg-green-25';
                              textColor = 'text-green-700';
                            } else if (!isCorrectAnswer && isUserSelected) {
                              // User selected wrong answer
                              borderColor = 'border-red-500';
                              bgColor = 'bg-red-50';
                              textColor = 'text-red-900';
                            }
                          }
                          
                          return (
                            <label
                              key={aIndex}
                              className={`flex items-start gap-3 p-3 border rounded-lg transition-colors ${
                                showResults ? '' : 'cursor-pointer hover:bg-gray-50'
                              } ${borderColor} ${bgColor}`}
                            >
                              <input
                                type="checkbox"
                                checked={isUserSelected}
                                onChange={() => !showResults && togglePreviewAnswer(question.id, aIndex)}
                                disabled={showResults}
                                className="mt-1 w-4 h-4"
                              />
                              <div className="flex-1">
                                <span className={textColor}>{answer.text || '(Empty answer)'}</span>
                                {showResults && isCorrectAnswer && (
                                  <span className="ml-2 text-green-600 font-medium"> Correct Answer</span>
                                )}
                                {showResults && !isCorrectAnswer && isUserSelected && (
                                  <span className="ml-2 text-red-600 font-medium"> Your Choice</span>
                                )}
                              </div>
                            </label>
                          );
                        })}
                      </div>
                    </div>
                  );
                })}
              </div>

              <div className="mt-6 pt-6 border-t">
                {!quizResults ? (
                  <button
                    onClick={() => {
                      const results = calculateScore();
                      setQuizResults(results);
                    }}
                    className="w-full bg-blue-500 text-white py-3 rounded-lg hover:bg-blue-600 font-medium"
                  >
                    Submit Quiz
                  </button>
                ) : (
                  <div className="space-y-4">
                    <div className="text-center p-4 bg-gray-50 rounded-lg">
                      <h3 className="text-lg font-bold mb-2">Quiz Results</h3>
                      <p className="text-2xl font-bold text-blue-600">
                        {quizResults.correct}/{quizResults.total} ({quizResults.percentage}%)
                      </p>
                      <p className="text-gray-600 mt-1">
                        {quizResults.percentage >= 70 ? 'Great job!' : 'Keep practicing!'}
                      </p>
                    </div>
                    <button
                      onClick={() => {
                        setQuizResults(null);
                        setPreviewAnswers({});
                      }}
                      className="w-full bg-green-500 text-white py-3 rounded-lg hover:bg-green-600 font-medium"
                    >
                      Retake Quiz
                    </button>
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  if (!isAuthenticated) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center p-4">
        <div className="bg-white p-8 rounded-lg shadow-md w-full max-w-sm">
          <h1 className="text-2xl font-bold mb-6 text-center">Quiz Pro</h1>
          <div className="space-y-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Your Name
              </label>
              <select
                value={selectedUser}
                onChange={(e) => setSelectedUser(e.target.value)}
                className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
              >
                <option value="">Select your name</option>
                {userNames.map(name => (
                  <option key={name} value={name}>{name}</option>
                ))}
              </select>
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Password
              </label>
              <input
                type="password"
                placeholder="Enter password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                onKeyPress={handleKeyPress}
                className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
              />
            </div>
            <div className="flex items-center">
              <input
                type="checkbox"
                id="rememberMe"
                checked={rememberMe}
                onChange={(e) => setRememberMe(e.target.checked)}
                className="w-4 h-4 mr-2"
              />
              <label htmlFor="rememberMe" className="text-sm text-gray-700">
                Remember me
              </label>
            </div>
            <button
              onClick={handleLogin}
              className="w-full bg-blue-500 text-white py-2 rounded-lg hover:bg-blue-600"
            >
              Login
            </button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50 flex flex-col md:flex-row">
      {/* Share Modal */}
      {showShareModal && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-lg p-6 max-w-md w-full">
            <h3 className="text-lg font-bold mb-4">Share Quiz</h3>
            <p className="text-gray-600 mb-4 text-sm">
              Anyone with this link can view and take the quiz. They can also edit it if they log in.
            </p>
            <div className="flex gap-2 mb-4">
              <input
                type="text"
                value={shareUrl}
                readOnly
                className="flex-1 px-3 py-2 border rounded-lg bg-gray-50 text-sm"
              />
              <button
                onClick={copyShareUrl}
                className="px-4 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600"
              >
                Copy
              </button>
            </div>
            <button
              onClick={() => setShowShareModal(false)}
              className="w-full border border-gray-300 py-2 rounded-lg hover:bg-gray-50"
            >
              Close
            </button>
          </div>
        </div>
      )}

      {/* Download Warning Modal */}
      {showDownloadWarning && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-lg p-6 max-w-md w-full">
            <h3 className="text-lg font-bold mb-2 text-red-600">Cannot Download Quiz</h3>
            <p className="text-gray-600 mb-4">
              Please fix the following issues before downloading:
            </p>
            <ul className="list-disc list-inside space-y-2 mb-6 text-sm text-gray-700">
              {downloadWarnings.map((warning, index) => (
                <li key={index}>{warning}</li>
              ))}
            </ul>
            <button
              onClick={() => setShowDownloadWarning(false)}
              className="w-full bg-blue-500 text-white py-2 rounded-lg hover:bg-blue-600"
            >
              OK
            </button>
          </div>
        </div>
      )}

      {/* Category Modal */}
      {showCategoryModal && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-lg p-6 max-w-sm w-full">
            <h3 className="text-lg font-bold mb-4">Add New Category</h3>
            <input
              type="text"
              value={newCategoryName}
              onChange={(e) => setNewCategoryName(e.target.value)}
              placeholder="Enter new category name"
              onKeyPress={(e) => {
                if (e.key === 'Enter' && newCategoryName.trim()) {
                  // Update current quiz with new category
                  const updatedQuiz = { ...currentQuiz, category: newCategoryName.trim() };
                  updateQuiz({ category: newCategoryName.trim() });
                  
                  // Save to persist the new category in the categories list
                  const savedQuiz = mockBackend.saveQuiz(updatedQuiz);
                  
                  // Update the quizzes list to reflect the change
                  setQuizzes(prev => {
                    const filtered = prev.filter(q => q.id !== savedQuiz.id);
                    return [savedQuiz, ...filtered].sort((a, b) => 
                      new Date(b.lastModified) - new Date(a.lastModified)
                    );
                  });
                  
                  // Reset and close
                  setNewCategoryName('');
                  setShowCategoryModal(false);
                }
              }}
              className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 mb-4"
              autoFocus
            />
            <div className="flex gap-2">
              <button
                onClick={() => {
                  setNewCategoryName('');
                  setShowCategoryModal(false);
                }}
                className="flex-1 px-4 py-2 border rounded-lg hover:bg-gray-50"
              >
                Cancel
              </button>
              <button
                onClick={() => {
                  if (newCategoryName.trim()) {
                    // Update current quiz with new category
                    const updatedQuiz = { ...currentQuiz, category: newCategoryName.trim() };
                    updateQuiz({ category: newCategoryName.trim() });
                    
                    // Save to persist the new category in the categories list
                    const savedQuiz = mockBackend.saveQuiz(updatedQuiz);
                    
                    // Update the quizzes list to reflect the change
                    setQuizzes(prev => {
                      const filtered = prev.filter(q => q.id !== savedQuiz.id);
                      return [savedQuiz, ...filtered].sort((a, b) => 
                        new Date(b.lastModified) - new Date(a.lastModified)
                      );
                    });
                    
                    // Reset and close
                    setNewCategoryName('');
                    setShowCategoryModal(false);
                  }
                }}
                className="flex-1 px-4 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600"
              >
                Add
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Preview Modal */}
      {showPreview && currentQuiz && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4 overflow-y-auto">
          <div className="bg-white rounded-lg p-6 max-w-2xl w-full my-8">
            <div className="flex justify-between items-start mb-6">
              <div>
                <h2 className="text-2xl font-bold">{currentQuiz.title}</h2>
                {currentQuiz.category && (
                  <p className="text-gray-600 mt-1">{currentQuiz.category}</p>
                )}
              </div>
              <div className="flex gap-2">
                {sharedQuizId && (
                  <button
                    onClick={handleEditFromPreview}
                    className="px-4 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600"
                  >
                    Edit
                  </button>
                )}
                <button
                  onClick={() => {
                    setShowPreview(false);
                    setPreviewAnswers({});
                  }}
                  className="p-2 hover:bg-gray-100 rounded"
                >
                  <X size={24} />
                </button>
              </div>
            </div>

            <div className="space-y-6">
              {currentQuiz.questions.map((question, qIndex) => {
                const questionResult = quizResults?.details.find(d => d.questionIndex === qIndex);
                const showResults = !!quizResults;
                
                return (
                  <div key={question.id} className="border-b pb-6 last:border-b-0">
                    <div className="flex items-start gap-3 mb-3">
                      <h3 className="font-medium text-lg flex-1">
                        {qIndex + 1}. {question.question || '(No question text)'}
                      </h3>
                      {showResults && (
                        <div className={`px-3 py-1 rounded-full text-sm font-medium ${
                          questionResult?.isCorrect 
                            ? 'bg-green-100 text-green-700' 
                            : 'bg-red-100 text-red-700'
                        }`}>
                          {questionResult?.isCorrect ? ' Correct' : ' Incorrect'}
                        </div>
                      )}
                    </div>
                    <div className="space-y-2">
                      {question.answers.map((answer, aIndex) => {
                        const isUserSelected = !!previewAnswers[`${question.id}-${aIndex}`];
                        const isCorrectAnswer = answer.correct;
                        
                        let borderColor = 'border-gray-200';
                        let bgColor = 'bg-white';
                        let textColor = 'text-gray-900';
                        
                        if (showResults) {
                          if (isCorrectAnswer && isUserSelected) {
                            // User selected correct answer
                            borderColor = 'border-green-500';
                            bgColor = 'bg-green-50';
                            textColor = 'text-green-900';
                          } else if (isCorrectAnswer && !isUserSelected) {
                            // Correct answer not selected
                            borderColor = 'border-green-300';
                            bgColor = 'bg-green-25';
                            textColor = 'text-green-700';
                          } else if (!isCorrectAnswer && isUserSelected) {
                            // User selected wrong answer
                            borderColor = 'border-red-500';
                            bgColor = 'bg-red-50';
                            textColor = 'text-red-900';
                          }
                        }
                        
                        return (
                          <label
                            key={aIndex}
                            className={`flex items-start gap-3 p-3 border rounded-lg transition-colors ${
                              showResults ? '' : 'cursor-pointer hover:bg-gray-50'
                            } ${borderColor} ${bgColor}`}
                          >
                            <input
                              type="checkbox"
                              checked={isUserSelected}
                              onChange={() => !showResults && togglePreviewAnswer(question.id, aIndex)}
                              disabled={showResults}
                              className="mt-1 w-4 h-4"
                            />
                            <div className="flex-1">
                              <span className={textColor}>{answer.text || '(Empty answer)'}</span>
                              {showResults && isCorrectAnswer && (
                                <span className="ml-2 text-green-600 font-medium"> Correct Answer</span>
                              )}
                              {showResults && !isCorrectAnswer && isUserSelected && (
                                <span className="ml-2 text-red-600 font-medium"> Your Choice</span>
                              )}
                            </div>
                          </label>
                        );
                      })}
                    </div>
                  </div>
                );
              })}
            </div>

            <div className="mt-6 pt-6 border-t">
              {!quizResults ? (
                <button
                  onClick={() => {
                    const results = calculateScore();
                    setQuizResults(results);
                  }}
                  className="w-full bg-blue-500 text-white py-3 rounded-lg hover:bg-blue-600 font-medium"
                >
                  Submit Quiz
                </button>
              ) : (
                <div className="space-y-4">
                  <div className="text-center p-4 bg-gray-50 rounded-lg">
                    <h3 className="text-lg font-bold mb-2">Quiz Results</h3>
                    <p className="text-2xl font-bold text-blue-600">
                      {quizResults.correct}/{quizResults.total} ({quizResults.percentage}%)
                    </p>
                    <p className="text-gray-600 mt-1">
                      {quizResults.percentage >= 70 ? 'Great job!' : 'Keep practicing!'}
                    </p>
                  </div>
                  <div className="flex gap-3">
                    <button
                      onClick={() => {
                        setQuizResults(null);
                        setPreviewAnswers({});
                      }}
                      className="flex-1 bg-green-500 text-white py-3 rounded-lg hover:bg-green-600 font-medium"
                    >
                      Retake Quiz
                    </button>
                    <button
                      onClick={() => setShowPreview(false)}
                      className="flex-1 bg-gray-500 text-white py-3 rounded-lg hover:bg-gray-600 font-medium"
                    >
                      Close
                    </button>
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
      )}

      {/* Delete Confirmation Modal */}
      {showDeleteConfirm && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-lg p-6 max-w-sm w-full">
            <h3 className="text-lg font-bold mb-2">Delete Quiz?</h3>
            <p className="text-gray-600 mb-4">
              Are you sure you want to delete "{showDeleteConfirm.title}"? This action cannot be undone.
            </p>
            <div className="flex gap-2 justify-end">
              <button
                onClick={() => setShowDeleteConfirm(null)}
                className="px-4 py-2 border rounded-lg hover:bg-gray-50"
              >
                Cancel
              </button>
              <button
                onClick={deleteQuizConfirmed}
                className="px-4 py-2 bg-red-500 text-white rounded-lg hover:bg-red-600"
              >
                Delete
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Sidebar */}
      <div className={`${sidebarOpen ? 'block' : 'hidden'} md:block fixed md:relative inset-0 md:inset-auto z-20 md:z-0 bg-white md:w-64 border-r flex flex-col h-screen md:h-auto`}>
        {/* Top Header Area */}
        <div className="p-6 border-b">
          <div className="flex justify-between items-center">
            <h1 
              onClick={() => {
                setCurrentQuiz(null);
                setSidebarView('all');
                setSelectedCategoryFilter('All');
              }}
              className="text-xl font-bold text-gray-800 cursor-pointer hover:text-blue-600"
            >
              Quiz Pro
            </h1>
            <div className="flex items-center gap-2">
              <button
                onClick={createNewQuiz}
                className="bg-blue-500 text-white px-3 py-1 text-sm rounded hover:bg-blue-600"
              >
                New Quiz
              </button>
            </div>
          </div>
        </div>

        {/* Current Quiz Details or Quizzes List */}
        {currentQuiz ? (
          // Show quiz details with navigation when editing
          <div className="flex-1 flex flex-col">
            {/* Navigation Slider */}
            <div className="px-4 py-3 bg-gray-50">
              <div className="flex bg-white rounded-lg p-1 border">
                <button
                  onClick={() => setSidebarView('all')}
                  className={`flex-1 py-2 px-3 text-sm rounded-md transition-colors ${
                    sidebarView === 'all'
                      ? 'bg-gray-800 text-white'
                      : 'text-gray-600 hover:bg-gray-100'
                  }`}
                >
                  All Quizzes
                </button>
                <button
                  onClick={() => setSidebarView('current')}
                  className={`flex-1 py-2 px-3 text-sm rounded-md transition-colors ${
                    sidebarView === 'current'
                      ? 'bg-gray-800 text-white'
                      : 'text-gray-600 hover:bg-gray-100'
                  }`}
                >
                  Quiz Editor
                </button>
              </div>
            </div>
            
            {sidebarView === 'current' ? (
              // Current Quiz Details
              <div className="flex-1 overflow-y-auto p-4 space-y-4">
                {/* Editable Quiz Title */}
                <div>
                  <label className="block text-xs font-medium text-gray-700 mb-1 uppercase tracking-wide">
                    Title
                  </label>
                  {editingTitle ? (
                    <input
                      type="text"
                      value={currentQuiz.title}
                      onChange={(e) => updateQuiz({ title: e.target.value })}
                      onBlur={() => setEditingTitle(false)}
                      onKeyPress={(e) => {
                        if (e.key === 'Enter') {
                          setEditingTitle(false);
                        }
                      }}
                      className="w-full text-sm font-medium bg-white border rounded px-2 py-1 focus:outline-none focus:ring-2 focus:ring-blue-500"
                      autoFocus
                    />
                  ) : (
                    <div>
                      <p 
                        onClick={() => setEditingTitle(true)}
                        className="text-sm font-medium text-gray-900 cursor-pointer hover:bg-gray-100 rounded px-2 py-1 -mx-2"
                      >
                        {currentQuiz.title || 'Untitled Quiz'}
                      </p>
                      <p className="text-xs text-gray-500 mt-1">
                        {currentQuiz.questions?.length || 0} Questions
                      </p>
                    </div>
                  )}
                </div>

                {/* Editable Status */}
                <div>
                  <label className="block text-xs font-medium text-gray-700 mb-1 uppercase tracking-wide">
                    Status
                  </label>
                  <select
                    value={currentQuiz.status || 'Draft'}
                    onChange={(e) => updateQuiz({ status: e.target.value })}
                    className={`w-full text-sm px-2 py-1 border rounded focus:outline-none focus:ring-2 focus:ring-blue-500 font-medium ${
                      (currentQuiz.status || 'Draft') === 'Draft' ? 'bg-gray-100 text-gray-700' :
                      (currentQuiz.status || 'Draft') === 'Need Approval' ? 'bg-yellow-100 text-yellow-700' :
                      (currentQuiz.status || 'Draft') === 'Approved' ? 'bg-blue-100 text-blue-700' :
                      (currentQuiz.status || 'Draft') === 'Archive' ? 'bg-gray-100 text-gray-600' :
                      'bg-green-100 text-green-700'
                    }`}
                  >
                    <option value="Draft">Draft</option>
                    <option value="Need Approval">Need Approval</option>
                    <option value="Approved">Approved</option>
                    <option value="Completed">Completed</option>
                    <option value="Archive">Archive</option>
                  </select>
                </div>

                {/* Category */}
                <div>
                  <label className="block text-xs font-medium text-gray-700 mb-2 uppercase tracking-wide">
                    Category
                  </label>
                  <div className="flex flex-wrap gap-2 mb-3">
                    <button
                      onClick={() => updateQuiz({ category: '' })}
                      className={`px-3 py-1 text-xs rounded-full transition-colors ${
                        !currentQuiz.category || currentQuiz.category === ''
                          ? 'bg-gray-800 text-white'
                          : 'bg-white text-gray-600 hover:bg-gray-100 border'
                      }`}
                    >
                      None
                    </button>
                    {getAllCategories().map(cat => (
                      <button
                        key={cat}
                        onClick={() => updateQuiz({ category: cat })}
                        className={`px-3 py-1 text-xs rounded-full transition-colors ${
                          currentQuiz.category === cat
                            ? 'bg-gray-800 text-white'
                            : 'bg-white text-gray-600 hover:bg-gray-100 border'
                        }`}
                      >
                        {cat}
                      </button>
                    ))}
                    <button
                      onClick={() => setShowCategoryModal(true)}
                      className="px-3 py-1 text-xs rounded-full bg-blue-100 text-blue-700 hover:bg-blue-200 border border-blue-300"
                    >
                      + New
                    </button>
                  </div>
                </div>

                {/* Delete Button */}
                <div>
                  <button
                    onClick={() => {
                      if (window.confirm('Are you sure you want to delete this quiz? This action cannot be undone.')) {
                        mockBackend.deleteQuiz(currentQuiz.id);
                        setQuizzes(prev => prev.filter(q => q.id !== currentQuiz.id));
                        setCurrentQuiz(null);
                      }
                    }}
                    className="w-full px-3 py-2 text-xs rounded border border-red-300 text-red-600 hover:bg-red-50"
                  >
                    Delete Quiz
                  </button>
                </div>

                {/* History */}
                {currentQuiz.history && currentQuiz.history.length > 0 && (
                  <div>
                    <label className="block text-xs font-medium text-gray-700 mb-2 uppercase tracking-wide">
                      History
                    </label>
                    <div className="space-y-2">
                      {(historyExpanded ? currentQuiz.history.slice().reverse() : currentQuiz.history.slice(-3).reverse()).map((entry, index) => (
                        <div key={index} className="text-xs bg-gray-50 p-2 rounded">
                          <div className="font-medium text-gray-900">{entry.user}</div>
                          <div className="text-gray-600">{entry.action}</div>
                          <div className="text-gray-400">
                            {new Date(entry.timestamp).toLocaleString('en-US', {
                              month: 'numeric',
                              day: 'numeric', 
                              year: 'numeric',
                              hour: 'numeric',
                              minute: '2-digit',
                              timeZoneName: 'short'
                            })}
                          </div>
                        </div>
                      ))}
                      {currentQuiz.history.length > 3 && (
                        <button
                          onClick={() => setHistoryExpanded(!historyExpanded)}
                          className="text-xs text-blue-500 hover:text-blue-700 underline"
                        >
                          {historyExpanded ? 'Show Less' : 'See More'}
                        </button>
                      )}
                    </div>
                  </div>
                )}
              </div>
            ) : (
              // All Quizzes List
              <div className="flex-1 flex flex-col">
                {/* Category Filter Pills */}
                <div className="px-4 py-3 border-b bg-gray-50">
                  <div className="flex flex-wrap gap-2">
                    {getCategoryFilterOptions().map(category => (
                      <button
                        key={category}
                        onClick={() => setSelectedCategoryFilter(category)}
                        className={`px-3 py-1 text-xs rounded-full transition-colors ${
                          selectedCategoryFilter === category
                            ? 'bg-gray-800 text-white'
                            : 'bg-white text-gray-600 hover:bg-gray-100 border'
                        }`}
                      >
                        {category}
                      </button>
                    ))}
                  </div>
                </div>
                
                {/* Quiz List */}
                <div className="flex-1 overflow-y-auto">
                  {getFilteredQuizzes().length === 0 ? (
                    <div className="p-4 text-center text-gray-500 text-sm">
                      {selectedCategoryFilter === 'All' ? 'No quizzes yet' : `No quizzes in ${selectedCategoryFilter}`}
                    </div>
                  ) : (
                    getFilteredQuizzes().map(quiz => (
                      <div
                        key={quiz.id}
                        className={`p-3 border-b hover:bg-gray-50 cursor-pointer ${currentQuiz?.id === quiz.id ? 'bg-blue-50' : ''}`}
                        onClick={() => {
                          setCurrentQuiz(quiz);
                          setHistory([JSON.parse(JSON.stringify(quiz))]);
                          setHistoryIndex(0);
                          setIsSaved(true);
                          setSidebarView('current');
                          setSidebarOpen(false);
                        }}
                      >
                        {/* Quiz Title and Status Row */}
                        <div className="flex items-center justify-between">
                          <div className="font-medium text-sm truncate pr-2">{quiz.title}</div>
                          {quiz.status && (
                            <span className={`inline-block px-2 py-0.5 rounded-full text-xs font-medium whitespace-nowrap ${
                              quiz.status === 'Approved' ? 'bg-green-100 text-green-700' :
                              quiz.status === 'Need Approval' ? 'bg-yellow-100 text-yellow-700' :
                              quiz.status === 'Completed' ? 'bg-blue-100 text-blue-700' :
                              quiz.status === 'Archive' ? 'bg-gray-100 text-gray-600' :
                              'bg-gray-100 text-gray-600'
                            }`}>
                              {quiz.status}
                            </span>
                          )}
                        </div>
                      </div>
                    ))
                  )}
                </div>
              </div>
            )}
          </div>
        ) : (
          // Show quizzes list when not editing
          <div className="flex-1 flex flex-col">
            {/* Category Filter Pills */}
            <div className="px-4 py-3 border-b bg-gray-50">
              <div className="flex flex-wrap gap-2">
                {getCategoryFilterOptions().map(category => (
                  <button
                    key={category}
                    onClick={() => setSelectedCategoryFilter(category)}
                    className={`px-3 py-1 text-xs rounded-full transition-colors ${
                      selectedCategoryFilter === category
                        ? 'bg-gray-800 text-white'
                        : 'bg-white text-gray-600 hover:bg-gray-100 border'
                    }`}
                  >
                    {category}
                  </button>
                ))}
              </div>
            </div>
            
            {/* Quiz List */}
            <div className="flex-1 overflow-y-auto">
              {getFilteredQuizzes().length === 0 ? (
                <div className="p-4 text-center text-gray-500 text-sm">
                  {selectedCategoryFilter === 'All' ? 'No quizzes yet' : `No quizzes in ${selectedCategoryFilter}`}
                </div>
              ) : (
                getFilteredQuizzes().map(quiz => (
                  <div
                    key={quiz.id}
                    className={`p-3 border-b hover:bg-gray-50 cursor-pointer ${currentQuiz?.id === quiz.id ? 'bg-blue-50' : ''}`}
                    onClick={() => {
                      setCurrentQuiz(quiz);
                      setHistory([JSON.parse(JSON.stringify(quiz))]);
                      setHistoryIndex(0);
                      setIsSaved(true);
                      setSidebarOpen(false);
                    }}
                  >
                    {/* Quiz Title and Status Row */}
                    <div className="flex items-center justify-between">
                      <div className="font-medium text-sm truncate pr-2">{quiz.title}</div>
                      {quiz.status && (
                        <span className={`inline-block px-2 py-0.5 rounded-full text-xs font-medium whitespace-nowrap ${
                          quiz.status === 'Approved' ? 'bg-green-100 text-green-700' :
                          quiz.status === 'Need Approval' ? 'bg-yellow-100 text-yellow-700' :
                          quiz.status === 'Completed' ? 'bg-blue-100 text-blue-700' :
                          quiz.status === 'Archive' ? 'bg-gray-100 text-gray-600' :
                          'bg-gray-100 text-gray-600'
                        }`}>
                          {quiz.status}
                        </span>
                      )}
                    </div>
                  </div>
                ))
              )}
            </div>
          </div>
        )}

        {/* Bottom Logout Button */}
        <div className="p-4 border-t mt-auto">
          <button
            onClick={() => {
              setIsAuthenticated(false);
              setCurrentUser('');
              localStorage.removeItem('quiz_auth');
              localStorage.removeItem('quiz_user');
            }}
            className="w-full bg-gray-200 text-gray-700 py-2 rounded-lg hover:bg-gray-300 flex items-center justify-center gap-2"
          >
            <LogOut size={18} />
            Logout
          </button>
        </div>
      </div>

      {/* Main Content */}
      <div className="flex-1 flex flex-col">
        {/* Top Bar - only show on mobile when no quiz selected, always show when quiz is selected */}
        {(!currentQuiz && (
          <div className="md:hidden bg-white border-b p-4 flex items-center justify-between gap-4">
            <div className="flex items-center gap-4">
              {sidebarOpen ? (
                <button
                  onClick={() => setSidebarOpen(false)}
                  className="p-2 hover:bg-gray-100 rounded"
                >
                  <X size={20} />
                </button>
              ) : (
                <button
                  onClick={() => setSidebarOpen(true)}
                  className="p-2 hover:bg-gray-100 rounded"
                >
                  <Menu size={20} />
                </button>
              )}
              {/* User Greeting */}
              <div className="text-xl font-bold text-gray-800">
                Hi, {editingUserName ? (
                  <select
                    value={currentUser}
                    onChange={(e) => {
                      setCurrentUser(e.target.value);
                      setEditingUserName(false);
                      localStorage.setItem('quiz_user', e.target.value);
                    }}
                    onBlur={() => setEditingUserName(false)}
                    className="text-gray-900 font-bold bg-transparent border-none focus:outline-none cursor-pointer"
                    autoFocus
                  >
                    {userNames.map(name => (
                      <option key={name} value={name}>{name}</option>
                    ))}
                  </select>
                ) : (
                  <span 
                    onClick={() => setEditingUserName(true)}
                    className="text-gray-900 font-bold cursor-pointer hover:text-blue-600"
                  >
                    {currentUser}
                  </span>
                )}
              </div>
            </div>
            <div className="flex-1 text-center">
              <h1 
                onClick={() => {
                  setCurrentQuiz(null);
                  setSidebarView('all');
                  setSelectedCategoryFilter('All');
                }}
                className="text-xl font-bold text-gray-800 cursor-pointer hover:text-blue-600"
              >
                Quiz Pro
              </h1>
            </div>
            <button
              onClick={createNewQuiz}
              className="bg-blue-500 text-white px-4 py-2 rounded-lg hover:bg-blue-600 flex items-center gap-2"
            >
              <Plus size={18} />
              <span className="hidden sm:inline">New Quiz</span>
            </button>
          </div>
        )) || (currentQuiz && (
          <div className="bg-white border-b p-6">
            <div className="max-w-3xl flex items-center justify-between gap-4">
              <div className="flex items-center gap-4">
                {sidebarOpen ? (
                  <button
                    onClick={() => setSidebarOpen(false)}
                    className="md:hidden p-2 hover:bg-gray-100 rounded"
                  >
                    <X size={20} />
                  </button>
                ) : (
                  <button
                    onClick={() => setSidebarOpen(true)}
                    className="md:hidden p-2 hover:bg-gray-100 rounded"
                  >
                    <Menu size={20} />
                  </button>
                )}
                
                {/* User Greeting */}
                <div className="text-xl font-bold text-gray-800">
                  Hi, {editingUserName ? (
                    <select
                      value={currentUser}
                      onChange={(e) => {
                        setCurrentUser(e.target.value);
                        setEditingUserName(false);
                        localStorage.setItem('quiz_user', e.target.value);
                      }}
                      onBlur={() => setEditingUserName(false)}
                      className="text-gray-900 font-bold bg-transparent border-none focus:outline-none cursor-pointer"
                      autoFocus
                    >
                      {userNames.map(name => (
                        <option key={name} value={name}>{name}</option>
                      ))}
                    </select>
                  ) : (
                    <span 
                      onClick={() => setEditingUserName(true)}
                      className="text-gray-900 font-bold cursor-pointer hover:text-blue-600"
                    >
                      {currentUser}
                    </span>
                  )}
                </div>
              </div>
              
              <div className="flex-1"></div>

              <div className="flex gap-2">
                <button
                  onClick={manualSave}
                  disabled={isSaved && !showSavedCheck}
                  className={`px-4 py-2 rounded-lg flex items-center gap-2 ${
                    showSavedCheck
                      ? 'bg-green-500 text-white'
                      : isSaved
                      ? 'bg-gray-300 text-gray-500 cursor-not-allowed'
                      : 'bg-blue-500 text-white hover:bg-blue-600'
                  }`}
                >
                  {showSavedCheck ? (
                    <>
                      Saved
                      <Check size={16} />
                    </>
                  ) : (
                    'Save'
                  )}
                </button>
                <button
                  onClick={() => setShowPreview(true)}
                  className="px-4 py-2 border border-gray-300 rounded-lg text-gray-700 hover:bg-gray-50"
                >
                  Preview
                </button>
                <button
                  onClick={generateShareUrl}
                  className="px-4 py-2 border border-gray-300 rounded-lg text-gray-700 hover:bg-gray-50 flex items-center gap-2"
                >
                  <Share2 size={18} />
                  <span className="hidden sm:inline">Share</span>
                </button>
                <button
                  onClick={exportToExcel}
                  disabled={!currentQuiz || currentQuiz.questions.length === 0}
                  className={`px-4 py-2 rounded-lg flex items-center gap-2 whitespace-nowrap ${
                    !currentQuiz || currentQuiz.questions.length === 0
                      ? 'bg-gray-300 text-gray-500 cursor-not-allowed'
                      : 'bg-green-500 text-white hover:bg-green-600'
                  }`}
                >
                  <Download size={18} />
                  Download
                </button>
              </div>
            </div>
          </div>
        ))}

        {/* Quiz Editor */}
        <div className="flex-1 overflow-y-auto p-4">
          {!currentQuiz ? (
            <div className="max-w-6xl">
              {quizzes.filter(q => q.status !== 'Completed').length > 0 && (
                <h2 className="text-2xl font-bold mb-6">Recently Modified</h2>
              )}
              {quizzes.filter(q => q.status !== 'Completed').length === 0 ? (
                <div className="text-center text-gray-500 mt-20">
                  <p className="mb-4">No quizzes yet. Click "New Quiz" to create your first one!</p>
                </div>
              ) : (
                <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4 mb-12">
                  {quizzes.filter(q => q.status !== 'Completed').map(quiz => (
                    <div
                      key={quiz.id}
                      onClick={() => {
                        setCurrentQuiz(quiz);
                        setHistory([JSON.parse(JSON.stringify(quiz))]);
                        setHistoryIndex(0);
                        setIsSaved(true);
                      }}
                      className="bg-white rounded-lg shadow-sm hover:shadow-md transition-shadow cursor-pointer border border-gray-200 aspect-square flex flex-col"
                    >
                      <div className="p-5 flex-1 flex flex-col">
                        <div className="flex-1">
                          <h3 className="font-semibold text-lg mb-2 line-clamp-2">{quiz.title}</h3>
                          <div className="flex items-center justify-between text-sm text-gray-600 mb-2">
                            <span>{quiz.questions?.length || 0} question{quiz.questions?.length !== 1 ? 's' : ''}</span>
                          </div>
                        </div>
                        <div className="mt-auto">
                          {quiz.status && (
                            <span className={`inline-block px-2 py-1 rounded text-xs font-medium ${
                              quiz.status === 'Approved' ? 'bg-green-100 text-green-700' :
                              quiz.status === 'Need Approval' ? 'bg-yellow-100 text-yellow-700' :
                              quiz.status === 'Completed' ? 'bg-blue-100 text-blue-700' :
                              'bg-gray-100 text-gray-600'
                            }`}>
                              {quiz.status}
                            </span>
                          )}
                          {quiz.category && (
                            <div className="text-xs text-gray-500 mt-2">
                              {quiz.category}
                            </div>
                          )}
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              )}

              {/* Completed/Archive Section */}
              {quizzes.filter(q => q.status === 'Completed').length > 0 && (
                <div className="mt-12 pt-8 border-t">
                  <h2 className="text-2xl font-bold mb-6">Completed</h2>
                  <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4">
                    {quizzes.filter(q => q.status === 'Completed').map(quiz => (
                      <div
                        key={quiz.id}
                        onClick={() => {
                          setCurrentQuiz(quiz);
                          setHistory([JSON.parse(JSON.stringify(quiz))]);
                          setHistoryIndex(0);
                          setIsSaved(true);
                        }}
                        className="bg-white rounded-lg shadow-sm hover:shadow-md transition-shadow cursor-pointer border border-gray-200 opacity-75 hover:opacity-100 aspect-square flex flex-col"
                      >
                        <div className="p-5 flex-1 flex flex-col">
                          <div className="flex-1">
                            <h3 className="font-semibold text-lg mb-2 line-clamp-2">{quiz.title}</h3>
                            <div className="flex items-center justify-between text-sm text-gray-600 mb-2">
                              <span>{quiz.questions?.length || 0} question{quiz.questions?.length !== 1 ? 's' : ''}</span>
                            </div>
                          </div>
                          <div className="mt-auto">
                            {quiz.status && (
                              <span className="inline-block px-2 py-1 rounded text-xs font-medium bg-blue-100 text-blue-700">
                                {quiz.status}
                              </span>
                            )}
                            {quiz.category && (
                              <div className="text-xs text-gray-500 mt-2">
                                {quiz.category}
                              </div>
                            )}
                          </div>
                        </div>
                      </div>
                    ))}
                  </div>
                </div>
              )}
            </div>
          ) : (
            <div className="max-w-3xl space-y-6">
              {/* Quiz Title Header */}
              <div className="border-b pb-4">
                <h1 className="text-3xl font-bold text-gray-900">{currentQuiz.title || 'Untitled Quiz'}</h1>
              </div>

              {/* Questions */}
              {currentQuiz.questions.map((question, qIndex) => {
                const isExpanded = expandedQuestions[question.id] ?? true; // Default to expanded
                
                return (
                  <div key={question.id} className="bg-white rounded-lg shadow-sm border overflow-hidden group">
                    {/* Question Header - Always Visible */}
                    <div 
                      className="p-4 cursor-pointer hover:bg-gray-200 border-b bg-gray-100"
                      onClick={() => toggleQuestionExpanded(question.id)}
                    >
                      <div className="flex items-center justify-between">
                        <div className="flex-1">
                          <div className="flex items-center gap-3">
                            <span className="text-sm font-medium text-gray-700">
                              {qIndex + 1}. {question.question || `Question ${qIndex + 1}`}
                            </span>
                          </div>
                        </div>
                        <div className="flex items-center gap-2">
                          {currentQuiz.questions.length > 1 && (
                            <>
                              {/* Desktop buttons with text - only visible on hover */}
                              <button
                                onClick={(e) => {
                                  e.stopPropagation();
                                  moveQuestion(qIndex, 'up');
                                }}
                                disabled={qIndex === 0}
                                className={`hidden md:flex px-3 py-1 text-xs rounded border items-center gap-1 opacity-0 group-hover:opacity-100 transition-opacity ${
                                  qIndex === 0
                                    ? 'text-gray-300 cursor-not-allowed border-gray-200'
                                    : 'text-gray-600 hover:bg-gray-50 border-gray-300'
                                }`}
                                title="Move question up"
                              >
                                Move Up
                              </button>
                              <button
                                onClick={(e) => {
                                  e.stopPropagation();
                                  moveQuestion(qIndex, 'down');
                                }}
                                disabled={qIndex === currentQuiz.questions.length - 1}
                                className={`hidden md:flex px-3 py-1 text-xs rounded border items-center gap-1 opacity-0 group-hover:opacity-100 transition-opacity ${
                                  qIndex === currentQuiz.questions.length - 1
                                    ? 'text-gray-300 cursor-not-allowed border-gray-200'
                                    : 'text-gray-600 hover:bg-gray-50 border-gray-300'
                                }`}
                                title="Move question down"
                              >
                                Move Down
                              </button>
                              
                              {/* Mobile buttons with icons */}
                              <button
                                onClick={(e) => {
                                  e.stopPropagation();
                                  moveQuestion(qIndex, 'up');
                                }}
                                disabled={qIndex === 0}
                                className={`md:hidden px-2 py-1 text-xs rounded ${
                                  qIndex === 0
                                    ? 'text-gray-300 cursor-not-allowed'
                                    : 'text-gray-600 hover:bg-gray-200 border'
                                }`}
                                title="Move question up"
                              >
                                
                              </button>
                              <button
                                onClick={(e) => {
                                  e.stopPropagation();
                                  moveQuestion(qIndex, 'down');
                                }}
                                disabled={qIndex === currentQuiz.questions.length - 1}
                                className={`md:hidden px-2 py-1 text-xs rounded ${
                                  qIndex === currentQuiz.questions.length - 1
                                    ? 'text-gray-300 cursor-not-allowed'
                                    : 'text-gray-600 hover:bg-gray-200 border'
                                }`}
                                title="Move question down"
                              >
                                
                              </button>
                            </>
                          )}
                          {currentQuiz.questions.length > 1 && (
                            <>
                              {/* Desktop remove button with text - only visible on hover */}
                              <button
                                onClick={(e) => {
                                  e.stopPropagation();
                                  deleteQuestion(qIndex);
                                }}
                                className="hidden md:flex px-3 py-1 text-xs rounded border border-red-300 text-red-600 hover:bg-red-50 items-center gap-1 opacity-0 group-hover:opacity-100 transition-opacity"
                                title="Delete question"
                              >
                                Remove
                              </button>
                              
                              {/* Mobile remove button with icon */}
                              <button
                                onClick={(e) => {
                                  e.stopPropagation();
                                  deleteQuestion(qIndex);
                                }}
                                className="md:hidden p-1 text-red-500 hover:bg-red-50 rounded"
                                title="Delete question"
                              >
                                <X size={16} />
                              </button>
                            </>
                          )}
                          <div className={`text-gray-400 ml-2 transform transition-transform ${
                            isExpanded ? 'rotate-90' : 'rotate-0'
                          }`}>
                            
                          </div>
                        </div>
                      </div>
                    </div>

                    {/* Question Details - Collapsible */}
                    {isExpanded && (
                      <div className="p-4 space-y-4">
                        {/* Question Input */}
                        <div>
                          <input
                            type="text"
                            value={question.question}
                            onChange={(e) => updateQuestion(qIndex, { question: e.target.value })}
                            className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                            placeholder="Enter question"
                          />
                        </div>

                        {/* Answers */}
                        <div>
                          <div className="space-y-2">
                            {question.answers.map((answer, aIndex) => (
                              <div key={aIndex} className="flex items-center gap-2">
                                <button
                                  onClick={() => updateAnswer(qIndex, aIndex, { correct: !answer.correct })}
                                  className={`p-2 rounded ${
                                    answer.correct
                                      ? 'bg-green-100 text-green-600'
                                      : 'bg-gray-100 text-gray-400 hover:bg-gray-200'
                                  }`}
                                  title={answer.correct ? 'Correct answer' : 'Mark as correct'}
                                >
                                  <Check size={18} />
                                </button>
                                <input
                                  type="text"
                                  value={answer.text}
                                  onChange={(e) => updateAnswer(qIndex, aIndex, { text: e.target.value })}
                                  className="flex-1 px-3 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                                  placeholder={`Answer ${aIndex + 1}`}
                                />
                                {question.answers.length > 1 && (
                                  <button
                                    onClick={() => deleteAnswer(qIndex, aIndex)}
                                    className="md:opacity-0 md:group-hover:opacity-100 transition-opacity p-2 text-red-500 hover:bg-red-50 rounded"
                                    title="Delete answer"
                                  >
                                    <X size={18} />
                                  </button>
                                )}
                              </div>
                            ))}
                          </div>

                          {question.answers.length < 4 && (
                            <button
                              onClick={() => addAnswer(qIndex)}
                              className="mt-3 text-sm text-blue-500 hover:text-blue-600"
                            >
                              + Add Answer
                            </button>
                          )}
                        </div>
                      </div>
                    )}
                  </div>
                );
              })}

              <button
                onClick={addQuestion}
                className="w-full py-3 border-2 border-dashed border-gray-300 rounded-lg text-gray-600 hover:border-blue-500 hover:text-blue-500"
              >
                + Add Question <span className="hidden sm:inline">(Ctrl+Enter)</span>
              </button>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default QuizCreator;
